<template>
  <el-table
    :data="tableData"
    stripe
    style="width: 100%">
    <el-table-column
      prop="date"
      label=""
      width="180">
    </el-table-column>
    <el-table-column
      prop="name"
      label=""
      width="180">
    </el-table-column>
    <el-table-column
      prop="address"
      label="">
    </el-table-column>
  </el-table>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      tableData: [{
        date: '2016-05-02',
        name: '',
        address: ' 1518 '
      }, {
        date: '2016-05-04',
        name: '',
        address: ' 1517 '
      }, {
        date: '2016-05-01',
        name: '',
        address: ' 1519 '
      }, {
        date: '2016-05-03',
        name: '',
        address: ' 1516 '
      }]
    }
  }
}
</script>



